<template>
    <div class="company-bg">
      <div class="position">
        <div class="position-warp">
          <span></span>  <span>位置:政企智慧服务平台</span> &nbsp;<span> > </span>&nbsp; <span>科技中小企业信息管理系统</span> &nbsp;<span> > </span>&nbsp; <span class="need-ting"  >企业机构</span>
          &nbsp;<span> > </span>&nbsp; <span class="need-ting" style="color: #555555;" >详情页</span>
        </div>
      </div>
      <div class="company-detail">
        <img :src="companyDetail.logo" alt="" class="company-logo">
        <div class="company-title">{{companyDetail.firmName}}</div>
        <div class="company-tabs">
             <ul>
               <li>
                 <div>{{companyDetail.typeEnterprise}}</div>
                 <div>企业类型</div></li>
               <li>
                 <div v-if="companyDetail">{{companyDetail.industry.split(';')[0]}}</div>
                 <div>行业</div></li>

               <li>
                 <div>{{companyDetail.registeredCapital}}万</div>
                 <div>注册资金</div></li>
               <li>
                 <div v-if="companyDetail">{{companyDetail.recordDate.substring(0,10)}}</div>
                 <div>注册时间</div></li>
               <li>
                 <div>{{companyDetail.scale}}人</div>
                 <div>从业人数</div></li>

             </ul>
        </div>
        <div class="detail-position">
          <img src="../../assets/cposition.png" alt=""  class="detail-img1">
          <span class="detai-span1">{{companyDetail.country  + companyDetail.city + companyDetail.district}}</span>
          <img src="../../assets/eye3.png" alt="" class="detail-img2">
          <span class="detai-span2">237</span>
        </div>
      </div>
      <div class="company-info">
        <div class="company-info1">企业简介</div>
        <div class="company-info2">{{companyDetail.enterpriseProfile}}</div>
        <div class="company-info1">科技成果</div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "TCompanyDetail",
        data(){
           return {
               companyDetail:''
           }
        },
        created() {
         this.getCompanyDetail()
        },
        methods:{
            //获取企业详情信息
            getCompanyDetail(){
                var that = this;
                var id  = that.$route.query.id;
                console.log(id)
                this.$axios.get('/bussinessCenter/findBussinessCenter',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        if (res.data.code==0){
                            that.companyDetail = res.data.data;
                        }
                        console.log(res)
                    })
            }
        }
    }
</script>

<style scoped lang="less">
  .position {
    height: 40px;
    .position-warp {
      width: 1209px;
      height: 100%;
      margin: 0 auto;
      line-height: 40px;
      color: #8a8a8a;
      font-size: 12px;
      position: relative;
      span:nth-child(1){
        display: inline-block;
        width: 13px;
        height: 19px;
        background: url("../../assets/position.png");
        background-size: 100% 100%;
        margin-top: 3px;
        position: absolute;
        top: 50%;
        transform: translateY(-71%);
      }
      span:nth-child(2){
        display: inline-block;
        padding-left: 24px;
      }
    }

  }
  .company-detail {
     width: 1200px;
    height: 209px;
    background-color: #ffffff;
    border: solid 1px #bcbcbc;
    margin: 14px auto;
    position: relative;
    box-sizing: border-box;
    padding: 48px 20px 40px 267px;
  }
  .company-bg {
    background-color: #F7F7F7;
  }
  .company-logo {
    width: 137px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 64px;
  }
  .company-title {
    color: #3a3a3a;
    font-size: 24px;
    font-family: PingFangSC-Semibold;
  }
  .company-tabs   {
      margin-top: 34px;
       ul {
           overflow: hidden;
          li {
            list-style: none;
            float: left;
             div:nth-child(1){
               color: #3a3a3a;
               font-size: 16px;
               font-family: PingFangSC-Regular;
               white-space: nowrap;
               overflow: hidden;
               text-overflow: ellipsis;
             }
            div:nth-child(2){
              color: #7f7f7f;
              font-size: 14px;
              margin-top: 8px;
            }
          }
         li:nth-child(1){
           width: 134px;
           height: 45px;
           text-align: left;
           border-right:  1px solid #e9e9e9;
         }
         li:nth-child(2){
           width: 210px;
           height: 45px;
           text-align: center;
           border-right:  1px solid #e9e9e9;
         }
         li:nth-child(3){
           width: 171px;
           height: 45px;
           text-align: center;
           border-right:  1px solid #e9e9e9;
         }
         li:nth-child(4){
           width: 171px;
           height: 45px;
           text-align: center;
           border-right:  1px solid #e9e9e9;
         }
         li:nth-child(5){
          padding-left: 30px;
         }
       }

  }
  .detail-position {
    position: absolute;
    top: 47px;
    right: 41px;
      .detail-img1{
        width: 15px;
        height: 17px;
        vertical-align: middle;
      }
    .detail-img2{
      width: 22px;
      height: 16px;
      vertical-align: middle;
    }
    .detai-span1 {
      color: #a1a1a1;
      font-size: 12px;
      font-weight: bold;
      vertical-align: middle;
      display: inline-block;
      margin-left: 4px;
      margin-right: 18px;
    }
    .detai-span2 {
      color: #a1a1a1;
      font-size: 12px;
      font-weight: bold;
      vertical-align: middle;
    }
  }
  .company-info {
    width: 1200px;
    height: 620px;
    background-color: #ffffff;
    border: solid 1px #bcbcbc;
    margin: 5px auto;
    box-sizing: border-box;
    padding: 56px 52px 20px 62px;
    .company-info1 {
      color: #3e3e3e;
      font-size: 18px;
      height: 38px;
      border-bottom: 1px solid #d2d2d2;
      position: relative;
    }
    .company-info1:before {
       content: '';
       display: block;
       width: 68px;
       height: 3px;
       background-color: #008fff;
       position: absolute;
       bottom: 0px;
       left: 0px;
    }
    .company-info2 {
      color: #8f8f8f;
      height: 169px;
      box-sizing: border-box;
      font-size: 16px;
      padding-top: 46px;
      padding-bottom: 60px;
    }
  }
</style>



